import React, { useEffect, useRef } from "react";
import { Popover } from "@material-ui/core";
import useChatStore from "../hooks/useChatStore";
import shallow from "zustand/shallow";

function AppPopover({ children, ...props }) {
  const [open, setOpen, anchorEl] = useChatStore(
    (state) => [state.open, state.setOpen, state.anchorEl],
    shallow
  );
  const id = open ? "chat-popover" : undefined;

  const handleClose = () => {
    setOpen(false);
  };

  return anchorEl ? (
    <Popover
      id={id}
      open={open}
      anchorEl={anchorEl}
      onClose={handleClose}
      anchorOrigin={{
        vertical: "bottom",
        horizontal: "right",
      }}
      transformOrigin={{
        vertical: "bottom",
        horizontal: "right",
      }}
      container={anchorEl?.parentNode}
      {...props}
    >
      {children}
    </Popover>
  ) : null;
}

export default AppPopover;
