import create from "zustand";

const useChatStore = create((set) => ({
  // state for chat configuration
  userId: null,
  chatId: null,
  endpoint: null,
  setConfig: ({ endpoint, userId, chatId }) =>
    set({ endpoint, userId, chatId }),
  // state for chat window anchor
  open: false,
  setOpen: (open) => set({ open }),
  anchorEl: null, // DOM element to anchor chat window to
  setAnchorEl: (anchorEl) => set({ anchorEl }),
  // state for error messaging
  error: null,
  setError: (error) => set({ error }),
  showError: false,
  setShowError: (showError) => set({ showError }),
  // state for message input
  message: "",
  setMessage: (message) => set({ message }),
  // state for conversation messages
  messages: [],
  setMessages: (messages) => set({ messages }),
  addMessage: (message) =>
    set((state) => ({ messages: [...state.messages, message] })),
  // responding states
  responding: false,
  setResponding: (responding) => set({ responding }),
}));

export default useChatStore;
