import React from "react";
import { render } from "react-dom";

import OnleaChat from "../../src";

var div = document.createElement("div");
// style based on config
document.body.appendChild(div);

function init(config) {
  // render the app into the `interventions` DOM element
  render(React.createElement(OnleaChat, config, null), div);
}

const joinButton = document.getElementById("join");
const userSelect = document.getElementById("users");
const chatSelect = document.getElementById("chats");

joinButton.addEventListener("click", function (event) {
  event.preventDefault();
  console.log("joining chat: ", userSelect.value, chatSelect.value);
  init({
    // endpoint: "http://ec2-34-207-243-133.compute-1.amazonaws.com:5000/",
    endpoint: "/",
    userId: userSelect.value,
    chatId: chatSelect.value,
  });
});

window.onload = (()=> {
  init({
    // endpoint: "http://ec2-34-207-243-133.compute-1.amazonaws.com:5000/",
    endpoint: "/",
    userId: userSelect.value,
    chatId: chatSelect.value,
  });

  // Kind of hacky
  document.querySelector('button[aria-label="open chat"]').click();
})
