import React from "react";
import shallow from "zustand/shallow";
import AnswerWindow from "../../Answer/components/AnswerWindow";
import clsx from "clsx";
import { withStyles } from "@material-ui/core";
import useAnswerStore from "../hooks/useAnswerStore";

const styles = (theme) => ({
  root: {
    transition: theme.transitions.create("transform"),
    transform: "translate3d(-100%, 0, 0)",
    position: "absolute",
    left: 0,
    top: 0,
    zIndex: 10000,
    background: "#fff",
  },
  show: {
    transform: "translate3d(0, 0, 0)",
  },
});

const AppAnswerWindow = ({ onSubmit, classes, className, ...props }) => {
  const [question, message, setQuestion] = useAnswerStore(
    (state) => [state.question, state.message, state.setQuestion],
    shallow
  );

  const handleCancel = () => {
    setQuestion("");
  };

  const handleSubmit = (response, event) => {
    const sourceMessage = question.context;
    const emitValue = {
      sourceMessage,
      answerMessage: message,
      response,
    };
    onSubmit && onSubmit(emitValue);
  };

  console.log("answer window", question);

  return (
    <AnswerWindow
      className={clsx(
        classes.root,
        { [classes.show]: Boolean(question) },
        className
      )}
      title="Add Answer"
      question={question?.context?.message?.message}
      onBack={handleCancel}
      onCancel={handleCancel}
      onSubmit={handleSubmit}
    />
  );
};

export default withStyles(styles)(AppAnswerWindow);
