import {
  Box,
  Button,
  TextField,
  Typography,
  withStyles,
} from "@material-ui/core";
import clsx from "clsx";
import React, { useEffect, useState } from "react";

const styles = (theme) => ({
  root: {
    padding: theme.spacing(2),
  },
  question: {
    "& textarea": {
      fontWeight: 700,
      color: theme.palette.text.primary,
    },
  },
  input: {
    marginTop: theme.spacing(2),
  },
  actions: {
    marginTop: theme.spacing(2),
  },
  button: {
    boxShadow: "none",
    "&.MuiButton-contained": {
      color: "#fff",
      marginRight: theme.spacing(1),
    },
    "&.MuiButton-contained.Mui-disabled": {
      color: "#999",
    },
    "&:hover": {
      boxShadow: "none",
    },
  },
});

const AnswerForm = ({ question, onCancel, onSubmit, classes, className }) => {
  const [answer, setAnswer] = useState("");
  const [keywords, setKeywords] = useState("");
  const [isValid, setIsValid] = useState(false);

  const handleAnswerChange = (event) => {
    setAnswer(event.target.value);
  };

  const handleKeywordChange = (event) => {
    setKeywords(event.target.value);
  };

  const handleSubmit = (event) => {
    const response = {
      answer: answer.trim(),
      keywords: keywords && keywords.split(",").map((k) => k.trim()),
    };
    onSubmit && onSubmit(response, event);
    setAnswer("");
    setKeywords("");
  };

  const handleCancel = () => {
    setAnswer("");
    setKeywords("");
    onCancel && onCancel();
  };

  // set if the response is valid
  useEffect(() => {
    setIsValid(Boolean(answer && answer.trim()));
  }, [answer]);

  return (
    <form
      className={clsx(classes.root, className)}
      noValidate
      autoComplete="off"
    >
      {/* <Typography className={classes.question}>{question}</Typography> */}
      <TextField
        id="answer-question"
        multiline
        className={clsx(classes.input, classes.question)}
        label="Question"
        value={question || " "}
        fullWidth
        disabled
        variant="outlined"
        InputLabelProps={{ shrink: true }}
      />
      <TextField
        id="answer-response"
        className={classes.input}
        label="Answer"
        multiline
        fullWidth
        rows={4}
        variant="outlined"
        value={answer}
        onChange={handleAnswerChange}
      />
      <TextField
        id="answer-keywords"
        className={classes.input}
        label="Keywords"
        fullWidth
        placeholder="comma separated keywords"
        variant="outlined"
        value={keywords}
        onChange={handleKeywordChange}
      />
      {/** additional options here (e.g. add link, add image, etc) */}
      <Box className={classes.actions}>
        <Button
          className={classes.button}
          variant="contained"
          disabled={!isValid}
          color="primary"
          onClick={handleSubmit}
        >
          Submit Answer
        </Button>
        <Button className={classes.button} onClick={handleCancel}>
          Cancel
        </Button>
      </Box>
    </form>
  );
};

export default withStyles(styles)(AnswerForm);
