import React from "react";
import { withStyles } from "@material-ui/core";
import ChatWindow from "../../Chat";
import useChatStore from "../hooks/useChatStore";
import shallow from "zustand/shallow";

const styles = (theme) => ({
  root: {},
});

function AppChatWindow({ ...props }) {
  const [
    responding,
    userId,
    message,
    setMessage,
    messages,
    setOpen,
  ] = useChatStore(
    (state) => [
      state.responding,
      state.userId,
      state.message,
      state.setMessage,
      state.messages,
      state.setOpen,
    ],
    shallow
  );

  const handleClose = () => {
    setOpen(false);
  };

  const handleChange = (event) => {
    const newValue = event.target.value;
    if (newValue !== message) setMessage(event.target.value);
  };

  return (
    <ChatWindow
      userId={userId}
      responding={responding}
      value={message}
      onChange={handleChange}
      messages={messages}
      onClose={handleClose}
      {...props}
    />
  );
}

export default withStyles(styles)(AppChatWindow);
