import React, { useEffect } from "react";
import ScopedCssBaseline from "@material-ui/core/ScopedCssBaseline";
import theme from "./theme";
import { makeStyles, ThemeProvider } from "@material-ui/core";
import AppPopover from "./components/AppPopover";
import AppChatButton from "./components/AppChatButton";
import AppChatWindow from "./components/AppChatWindow";
import useSocketClient from "./hooks/useSocketClient";
import useChatStore from "./hooks/useChatStore";
import useAnswerStore from "./hooks/useAnswerStore";
import AppAnswerWindow from "./components/AppAnswerWindow";

const useStyles = makeStyles((theme) => ({
  button: {
    position: "fixed",
    bottom: theme.spacing(3),
    right: theme.spacing(3),
    width: theme.spacing(8),
    height: theme.spacing(8),
  },
}));

function App({
  endpoint = "localhost:5000",
  userId = "user1",
  chatId = "abcd",
}) {
  // style overrides
  const classes = useStyles();

  // set configuration in the store
  const setConfig = useChatStore((state) => state.setConfig);
  useEffect(() => {
    setConfig({ endpoint, userId, chatId });
  }, [endpoint, userId, chatId, setConfig]);

  const setMessage = useChatStore((state) => state.setMessage);
  const setQuestion = useAnswerStore((state) => state.setQuestion);

  // initialize socket client for server communication
  const { sendMessage, sendAction, sendAnswer } = useSocketClient({
    onSendComplete: () => setMessage(""),
    onAnswerComplete: () => setQuestion(""),
  });

  // handler for when messages are sent
  const handleSend = (event) => {
    if (!event?.target?.value) return;
    const trimmedMessage = event.target.value.trim();
    if (!trimmedMessage) return;
    event.preventDefault();
    sendMessage(trimmedMessage);
  };

  const handleAction = (actionId, message) => {
    // get action from message
    const action = message?.actions?.find((a) => a.id === actionId);
    if (actionId === "escalate" && action) {
      sendAction(actionId, action, message);
    }
    if (actionId === "answer" && action) {
      console.log("show answer interface!", action, message);
      setQuestion(action, message);
    }
  };

  const handleSubmitAnswer = (response, event) => {
    sendAnswer(response);
  };

  return (
    <ThemeProvider theme={theme}>
      <ScopedCssBaseline>
        <AppChatButton className={classes.button} />
        <AppPopover>
          <AppChatWindow onSend={handleSend} onAction={handleAction} />
          <AppAnswerWindow onSubmit={handleSubmitAnswer} />
        </AppPopover>
      </ScopedCssBaseline>
    </ThemeProvider>
  );
}

export default App;
