import { useCallback, useEffect } from "react";
import io from "socket.io-client";
import shallow from "zustand/shallow";
import useChatStore from "./useChatStore";

let socket;

/**
 * This hook handles all communication to and from the server
 */
export default function useSocketClient({
  onSendComplete,
  onActionComplete,
  onAnswerComplete,
  ...options
}) {
  const [
    endpoint,
    userId,
    chatId,
    setResponding,
    setMessages,
    addMessage,
  ] = useChatStore(
    (state) => [
      state.endpoint,
      state.userId,
      state.chatId,
      state.setResponding,
      state.setMessages,
      state.addMessage,
    ],
    shallow
  );

  console.log({endpoint})

  /** Setup event listeners from server */
  useEffect(() => {
    if (!endpoint || !chatId || !userId) return;
    socket = io(endpoint);
    socket.emit("join", { userId, chatId });
    // handle loading chat history
    socket.on("messages", (messages) => {
      console.log("received messages", messages);
      setMessages(messages);
    });
    // handle adding individual messages
    socket.on("message", (message) => {
      console.log("received message", message);
      addMessage(message);
    });
    // handle "responding" state changes
    socket.on("responding", (isResponding) => setResponding(isResponding));
    return () => {
      socket.emit();
    };
  }, [endpoint, userId, chatId, setMessages, addMessage, setResponding]);

  /**
   * Send message to server
   */
  const sendMessage = useCallback(
    (message) => {
      if (!socket) return;
      socket.emit("sendMessage", { userId, chatId, message }, onSendComplete);
    },
    [onSendComplete, userId, chatId]
  );

  /**
   * Send action to server
   */
  const sendAction = useCallback(
    (id, action, message) => {
      if (!socket) return;
      socket.emit(
        "action",
        { id, action, message, userId, chatId },
        onActionComplete
      );
    },
    [onActionComplete, userId, chatId]
  );

  /**
   * Send answer to server
   */
  const sendAnswer = useCallback(
    ({ response, answerMessage, sourceMessage }) => {
      if (!socket) return;
      socket.emit(
        "sendAnswer",
        { response, answerMessage, sourceMessage, userId, chatId },
        onAnswerComplete
      );
    },
    [onAnswerComplete, userId, chatId]
  );

  return {
    sendMessage,
    sendAction,
    sendAnswer,
  };
}
