import React from "react";
import PropTypes from "prop-types";
import {
  Avatar,
  Box,
  Button,
  styled,
  Typography,
  withStyles,
} from "@material-ui/core";
import clsx from "clsx";
import { timeFormat } from "d3-time-format";
import Linkify from "linkifyjs/lib/linkify-react";
const formatTime = timeFormat("%b %d, %I:%M %p");

const styles = (theme) => ({
  root: { margin: theme.spacing(2), position: "relative" },
  avatar: {
    position: "absolute",
    top: 0,
    left: 0,
  },
  messages: {
    position: "relative",
    backgroundColor: theme.palette.grey[200],
    padding: theme.spacing(2),
    display: "flex",
    flexDirection: "column-reverse",
    "&:after": {
      content: '""',
      width: 0,
      height: 0,
      position: "absolute",
      border: `16px solid transparent`,
      borderTopColor: theme.palette.grey[200],
    },
    "& .MuiTypography-root:not(:last-child)": {
      marginTop: theme.spacing(1),
    },
    // "& .MuiTypography-root:first-child": {
    //   marginTop: 0,
    // },
  },
  them: {
    marginLeft: theme.spacing(6),
    marginRight: theme.spacing(0),
    borderRadius: `0 8px 8px 8px`,
    "&:after": {
      left: -8,
      top: 0,
    },
    "& + $date": {
      marginLeft: theme.spacing(7),
    },
  },
  you: {
    marginLeft: theme.spacing(0),
    marginRight: theme.spacing(1),
    borderRadius: `8px 0 8px 8px`,
    background: theme.palette.primary.main,
    color: "#fff",
    "&:after": {
      right: -8,
      top: 0,
      borderTopColor: theme.palette.primary.main,
    },
    "& + $date": {
      textAlign: "right",
      marginRight: theme.spacing(2),
    },
  },
  date: {
    color: theme.palette.grey[500],
    marginTop: theme.spacing(0.5),
    display: "block",
  },
  action: {
    marginTop: theme.spacing(1),
  },
});

const ChatAction = ({ id, type, className, onClick, style, ...rest }) => {
  if (type === "button") {
    return (
      <Button
        className={className}
        variant="outlined"
        style={style}
        onClick={onClick}
      >
        {rest.label || id}
      </Button>
    );
  }
  if (type === "image") {
    return (
      <img
        style={{ maxWidth: "100%", maxHeight: "300", ...style }}
        className={className}
        src={rest.src}
      />
    );
  }
  return null;
};

const ChatMessage = ({
  messages,
  user,
  lastDate,
  you,
  userId,
  extras,
  onAction,
  classes,
  className,
  ...props
}) => {
  return (
    <Box className={clsx(classes.root, className)} {...props}>
      {!you && user && (
        <Avatar alt={user.name} src={user.avatar} className={classes.avatar} />
      )}
      <Box
        className={clsx(classes.messages, {
          [classes.you]: you,
          [classes.them]: !you,
        })}
      >
        {messages.map(({ id, message, actions, context }, i) => (
          <React.Fragment key={id}>
            {actions &&
              actions.map((action) => (
                <ChatAction
                  key={action.id}
                  className={classes.action}
                  onClick={(e) => onAction && onAction(action.id, messages[i])}
                  {...action}
                />
              ))}
            <Typography key={id}>
              <Linkify>{message}</Linkify>
            </Typography>
          </React.Fragment>
        ))}
        {extras}
      </Box>
      {lastDate && (
        <Typography className={classes.date} variant="caption">
          {formatTime(lastDate)}
        </Typography>
      )}
    </Box>
  );
};

ChatMessage.propTypes = {
  message: PropTypes.object,
  classes: PropTypes.object,
  className: PropTypes.string,
};

export default withStyles(styles)(ChatMessage);
